'use client'

import { useState } from 'react'
import Image from 'next/image'
import Link from 'next/link'
import { Card, CardContent } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'

// Mock matches data
const mockMatches = [
  {
    id: '1',
    petId: '1',
    name: 'Buddy',
    breed: 'Golden Retriever',
    age: 3,
    image: 'https://images.unsplash.com/photo-1552053831-71594a27632d?w=300&h=300&fit=crop',
    matchedAt: '2024-01-15',
    lastMessage: 'Hi! Buddy would love to meet Luna for a playdate!',
    owner: 'Sarah Johnson',
    preferences: ['playdate', 'breeding']
  },
  {
    id: '2',
    petId: '2',
    name: 'Luna',
    breed: 'Border Collie',
    age: 2,
    image: 'https://images.unsplash.com/photo-1551717743-49959800b1f6?w=300&h=300&fit=crop',
    matchedAt: '2024-01-14',
    lastMessage: 'That sounds great! When would work best?',
    owner: 'Mike Chen',
    preferences: ['playdate', 'adoption']
  },
  {
    id: '3',
    petId: '3',
    name: 'Max',
    breed: 'German Shepherd',
    age: 4,
    image: 'https://images.unsplash.com/photo-1589941013453-ec89f33b5e95?w=300&h=300&fit=crop',
    matchedAt: '2024-01-13',
    lastMessage: 'Max is very well-behaved and would be perfect for breeding.',
    owner: 'Emily Davis',
    preferences: ['breeding', 'playdate']
  }
]

export default function MatchesPage() {
  const [matches] = useState(mockMatches)

  const getPreferenceColor = (preference: string) => {
    switch (preference) {
      case 'playdate':
        return 'bg-blue-100 text-blue-800'
      case 'adoption':
        return 'bg-green-100 text-green-800'
      case 'breeding':
        return 'bg-purple-100 text-purple-800'
      default:
        return 'bg-gray-100 text-gray-800'
    }
  }

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    const now = new Date()
    const diffTime = Math.abs(now.getTime() - date.getTime())
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
    
    if (diffDays === 1) return 'Yesterday'
    if (diffDays < 7) return `${diffDays} days ago`
    return date.toLocaleDateString()
  }

  if (matches.length === 0) {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen p-6">
        <div className="text-center">
          <div className="text-6xl mb-4">ðŸ’•</div>
          <h2 className="text-2xl font-bold mb-4">No matches yet</h2>
          <p className="text-gray-600 mb-6">
            Keep swiping to find the perfect matches for your pet!
          </p>
          <Link href="/">
            <Button className="bg-primary hover:bg-primary/90">
              Start Swiping
            </Button>
          </Link>
        </div>
      </div>
    )
  }

  return (
    <div className="container mx-auto px-4 py-6">
      <div className="mb-6">
        <h1 className="text-2xl font-bold mb-2">Your Matches</h1>
        <p className="text-gray-600">
          {matches.length} pet{matches.length !== 1 ? 's' : ''} matched with you
        </p>
      </div>

      <div className="space-y-4">
        {matches.map((match) => (
          <Card key={match.id} className="overflow-hidden hover:shadow-md transition-shadow">
            <CardContent className="p-0">
              <div className="flex">
                {/* Pet Image */}
                <div className="relative w-24 h-24 flex-shrink-0">
                  <Image
                    src={match.image}
                    alt={match.name}
                    fill
                    className="object-cover"
                    sizes="96px"
                  />
                </div>

                {/* Match Info */}
                <div className="flex-1 p-4">
                  <div className="flex items-start justify-between mb-2">
                    <div>
                      <h3 className="font-semibold text-lg">{match.name}</h3>
                      <p className="text-sm text-gray-600">
                        {match.breed} â€¢ {match.age} years
                      </p>
                      <p className="text-xs text-gray-500">
                        Owner: {match.owner}
                      </p>
                    </div>
                    <span className="text-xs text-gray-400">
                      {formatDate(match.matchedAt)}
                    </span>
                  </div>

                  {/* Preferences */}
                  <div className="flex flex-wrap gap-1 mb-3">
                    {match.preferences.map((pref) => (
                      <Badge
                        key={pref}
                        variant="secondary"
                        className={`text-xs ${getPreferenceColor(pref)}`}
                      >
                        {pref}
                      </Badge>
                    ))}
                  </div>

                  {/* Last Message */}
                  <p className="text-sm text-gray-700 mb-3 line-clamp-2">
                    {match.lastMessage}
                  </p>

                  {/* Action Button */}
                  <Link href={`/chat/${match.id}`}>
                    <Button size="sm" className="w-full">
                      Send Message
                    </Button>
                  </Link>
                </div>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>

      {/* New Matches Section */}
      <div className="mt-8 text-center">
        <p className="text-gray-600 mb-4">Looking for more matches?</p>
        <Link href="/">
          <Button variant="outline">
            Continue Swiping
          </Button>
        </Link>
      </div>
    </div>
  )
}
